.about-wrapper {
    color: white;
    background-color: transparent;
    text-align: center;
    height: 98vh;
    background-image: url(../images/my-pic.png);
    background-repeat: no-repeat;
    background-size: clamp(23.125rem, -30.3125rem + 112.5vh, 40rem);
    background-position: bottom center;
}
.about-heading {
    font-family: 'Lobster', cursive;
    font-size: clamp(2.8rem, 1.7647rem + 3.7647vw, 6rem);
    margin-bottom: 0.4em;
}
.about-para {
    margin: 0 auto;
    line-height: 1.4em;
    font-size: clamp(1rem, 0.9714rem + 0.1143vw, 1.1rem);
    margin-bottom: 2em;
    max-width: 900px;
}
.line {
    width: 30%;
    min-width: 300px;
    margin: 0 auto;
    color: white;
}
.social-icons {
    margin: 1em auto 0 auto;
    width: 150px;
    display: flex;
    justify-content: space-between;
}

@media (min-height: 750px) {
    .about-wrapper {
        height: 90vh;
    }
}

 